@import "src/styles/colors.module";
@import "src/styles/sizes.module";

.ContactsPage {
  display: flex;
  flex-direction: column;
  height: 100vh;

  &Header {
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 44px;
    color: $dark-font-color;
    display: flex;
    align-items: center;

    @media (max-width: $medium-size) {
      font-size: 28px;
    }
  }

  &Link {
    padding-left: 5px;
    font-style: normal;
    font-weight: 300;
    font-size: 36px;
    line-height: 44px;
    color: $dark-font-color;
    display: flex;
    flex-wrap: wrap;
    text-decoration: none;

    &:hover {
      color: $red-color;
    }

    @media (max-width: $medium-size) {
      font-size: 25px;
    }
  }

  &Hidden {
    display: none;
    content: none;
    color: transparent;
    opacity: 0;
  }
}
